@use "../../GlobalStyles//breakPoints.scss" as *;
.gallery {
   position: relative;
   &_grid {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-auto-rows: 240px 120px;
      grid-auto-flow: dense;
      gap: 10px;

      @include bpMin(m) {
         grid-auto-rows: 340px 170px;
         grid-template-columns: repeat(4, 1fr);
      }
      @include bpMin(l) {
         grid-auto-rows: 400px 200px;
         margin: 0 120px;
      }

      & > div {
         width: 100%;
         height: 100%;
         overflow: hidden;
         position: relative;
         cursor: pointer;
         &:nth-child(4),
         &:nth-child(7),
         &:nth-child(12),
         &:nth-child(14),
         &:nth-child(15),
         &:nth-child(18),
         &:nth-child(20),
         &:nth-child(24) {
            grid-column: span 2;
         }

         &:nth-child(5),
         &:nth-child(15) {
            grid-row: span 2;
            grid-column: span 2;
         }
         &:nth-child(22) {
            grid-row: span 2;
         }
         &:nth-child(7),
         &:nth-child(14),
         &:nth-child(17),
         &:nth-child(23) {
            @include bpMin(l) {
               grid-row: span 2;
            }
         }

         & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: transform 0.3s ease-in-out;
         }
         &:hover img {
            transform: scale(1.2);
         }

         & .overlayy {
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            background: rgba(0, 0, 0, 0.5);
            opacity: 0;
            transition: opacity 0.3s ease-in-out;
            z-index: 1;
            display: flex;
            align-items: center;
            justify-content: center;
            color: #fff;
            font-size: 22px;
         }
         &:hover .overlayy {
            opacity: 1;
         }
      }
   }
   & .image-display {
      position: fixed;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      background: rgba(0, 0, 0, 0.9);
      display: none;
      z-index: 9;

      &.show {
         display: flex;
         align-items: center;
         justify-content: center;
      }

      & img {
         height: 100%;
         @include bpMax(m) {
            height: 80%;
         }
      }
      & button {
         position: absolute;
         right: 100px;
         background: #fff;
         color: #000;
         height: 30px;
         width: 30px;
         border-radius: 50%;
         display: flex;
         align-items: center;
         justify-content: center;

         @include bpMax(l) {
            left: 50%;
            transform: translateX(-50%);
            bottom: 50px;
         }
      }
   }
}
